/**
 * Email Archive Worker
 * Archives incoming emails to in-memory storage for testing
 * No external dependencies - pure Cloudflare Worker
 */

// In-memory storage for testing
const storage = new Map<string, Uint8Array>();

export type Env = {
  STORAGE_TYPE?: string;
  BUCKET_NAME?: string;
}

export type EmailMessage = {
  from: string;
  to: string;
  headers: Headers;
  raw: () => Promise<ArrayBuffer>;
  setReject?: (reason: string) => void;
  forward?: (to: string) => Promise<void>;
}

// Result types for email processing operations
type EmailProcessingSuccess = {
  ok: true;
  data: {
    key: string;
    metadata: EmailMetadata;
  };
}

type EmailProcessingError = {
  ok: false;
  error: {
    type: 'raw_content_fetch' | 'storage_write' | 'metadata_extraction';
    message: string;
    cause?: unknown;
  };
}

type EmailProcessingResult = EmailProcessingSuccess | EmailProcessingError;

type EmailMetadata = {
  messageId: string;
  from: string;
  to: string;
  subject: string;
  receivedAt: string;
  size: number;
}

/**
 * Process email for archiving
 * Returns Result type instead of throwing exceptions
 */
async function processEmail(message: EmailMessage): Promise<EmailProcessingResult> {
  // Get raw email content
  let rawEmail: ArrayBuffer;
  try {
    rawEmail = await message.raw();
  } catch (cause) {
    return {
      ok: false,
      error: {
        type: 'raw_content_fetch',
        message: 'Failed to fetch raw email content',
        cause
      }
    };
  }

  // Extract metadata
  let metadata: EmailMetadata;
  try {
    metadata = {
      messageId: message.headers.get("message-id") || `test-${Date.now()}`,
      from: message.from,
      to: message.to,
      subject: message.headers.get("subject") || "No subject",
      receivedAt: new Date().toISOString(),
      size: rawEmail.byteLength
    };
  } catch (cause) {
    return {
      ok: false,
      error: {
        type: 'metadata_extraction',
        message: 'Failed to extract email metadata',
        cause
      }
    };
  }

  // Generate storage keys with date-based path
  let baseKey: string;
  try {
    const date = new Date();
    const datePrefix = `emails/${date.getFullYear()}/${String(date.getMonth() + 1).padStart(2, '0')}/${String(date.getDate()).padStart(2, '0')}`;
    baseKey = `${datePrefix}/${metadata.messageId}`;

    // Store in memory
    storage.set(`${baseKey}.eml`, new Uint8Array(rawEmail));
    storage.set(`${baseKey}.json`, new TextEncoder().encode(JSON.stringify(metadata, null, 2)));
  } catch (cause) {
    return {
      ok: false,
      error: {
        type: 'storage_write',
        message: 'Failed to write email to storage',
        cause
      }
    };
  }

  return {
    ok: true,
    data: {
      key: baseKey,
      metadata
    }
  };
}

export default {
  async fetch(request: Request, env: Env, ctx: ExecutionContext): Promise<Response> {
    const url = new URL(request.url);
    
    // Test endpoint for email submission
    if (url.pathname === "/__email") {
      const data = await request.json() as {
        from: string;
        to: string;
        headers?: Record<string, string>;
        body?: string;
      };
      
      const message: EmailMessage = {
        from: data.from,
        to: data.to,
        headers: new Headers(data.headers || {}),
        async raw() {
          return new TextEncoder().encode(data.body || "").buffer;
        }
      };
      
      // Process email
      const result = await processEmail(message);
      
      if (result.ok) {
        return new Response(JSON.stringify({
          status: "success",
          message: "Email archived successfully",
          key: result.data.key
        }), {
          status: 200,
          headers: { "Content-Type": "application/json" }
        });
      } else {
        return new Response(JSON.stringify({
          status: "error",
          type: result.error.type,
          message: result.error.message
        }), {
          status: 500,
          headers: { "Content-Type": "application/json" }
        });
      }
    }
    
    // Storage inspection endpoint
    if (url.pathname === "/__storage") {
      const contents = Array.from(storage.entries()).map(([k, v]) => ({
        key: k,
        size: v.length
      }));
      return new Response(JSON.stringify(contents, null, 2), {
        headers: { "Content-Type": "application/json" }
      });
    }
    
    // Health check endpoint
    if (url.pathname === "/__health") {
      return new Response(JSON.stringify({
        status: 'ok',
        timestamp: new Date().toISOString(),
        mode: 'in-memory-storage',
        storageSize: storage.size
      }, null, 2), {
        headers: { "Content-Type": "application/json" }
      });
    }
    
    return new Response("Email Archive Worker", {
      headers: { "Content-Type": "text/plain" }
    });
  },
  
  async email(message: EmailMessage, env: Env, ctx: ExecutionContext): Promise<Response> {
    const result = await processEmail(message);
    
    if (result.ok) {
      return new Response(JSON.stringify({
        status: "success",
        message: "Email archived successfully",
        key: result.data.key
      }), {
        status: 200,
        headers: { "Content-Type": "application/json" }
      });
    } else {
      return new Response(JSON.stringify({
        status: "error",
        type: result.error.type,
        message: result.error.message
      }), {
        status: 500,
        headers: { "Content-Type": "application/json" }
      });
    }
  }
};